.navbar__container {
  display: flex;
  justify-content: center;
  height: 62px;
  background-color: rgb(223, 223, 223);
}

.navbar__text {
  margin: 15px 20px 0px 20px;
  font-size: 20px;
  cursor: pointer;
}

.navbar__text:hover {
  text-decoration: underline;
}

.App {
  text-align: center;
  font-family: "Montserrat";
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.app__sc-flex1 {
  width: 300px;
  height: auto;
  color: rgb(226, 223, 223);
  text-align: left;
}

.programmer__c {
  padding: 0px 0px 0px 0px;
  margin: 0px 0px 0px 0px;
  width: 100%;
  background-color: #aedae5;
  color: #083f6a;
  overflow-y: scroll;
  font-family: "Ubuntu Mono";
  text-align: left;
}

.programmer__c--flex {
  display: flex;
}

.programmer__name-title {
  padding: 50px 0px 0px 0px;
  margin: 0px 0px 0px 30px;
  text-align: left;
}

.programmer__name-subtitle {
  padding: 5px 0px 0px 0px;
  margin: 0px 0px 20px 30px;
  text-align: left;
}

.programmer__sc-flex2__title {
  margin: 0px 20px 40px 20px;
  font-size: 55px;
  font-weight: 1000;
}

.programmer__sc-flex2__subtitle {
  margin: 50px 10px 0px 20px;
  font-size: 20px;
}

.programmer__sc-flex2__container-title {
  margin: 3px 10px 3px 20px;
  font-size: 20px;
  font-weight: 600;
}

.programmer__sc-flex2__card-overflow {
  width: 95%;
  margin-left: 1.5%;
  margin-top: 20px;
  margin-bottom: 20px;
}

