.fd-rvw-cntnr {
  margin: 0px 20px 20px 20px;
}

.fd-rvw-cntnr__see-more--inline {
  display: inline;
  text-decoration: underline;
}

.fd-rvw-cntnr__see-more--inline:hover {
  cursor: pointer;
}

.fd-rvw-cntnr__see-more {
  margin-bottom: 0px;
  text-decoration: underline;
}

.fd-rvw-cntnr__see-more:hover {
  cursor: pointer;
}

.idiot__c {
  padding: 0px 0px 0px 0px;
